<template>
  <v-container
    fluid
  >
    <v-row>
      <v-col
        cols="8"
      >
        <base-material-card
          class="px-5 py-3 "
          color="indigo"
          icon="mdi-vuetify"
          inline
        >
          <template v-slot:after-heading>
            <div class="display-2 font-weight-light">
              {{ $t('菜品信息') }}
            </div>
          </template>
          <v-divider class="mt-3"/>
          <v-data-table
            :fixed-header="true"
            :headers="headers"
            :items="orders"
            :items-per-page="15"
            :loading="loading"
            item-key="id"
            multi-sort
            @reloaded="loadDetail(id)"
          >
            <template v-slot:item.name="{ item }">
              {{ item.name }}
              <span
                v-if="item.hasMod>0"
                class="red--text font-weight-bold"
              >*</span>
            </template>
            <template v-slot:item.mod="{ item }">
              <div
                :colspan="headers.length"
              >
                <dish-modification :dish-item="item"/>
              </div>
            </template>
          </v-data-table>
        </base-material-card>
        <ik-data-table
          v-if="billData.billInfo.paymentStatus==='1'"
          ref="paymentLog"
          :model="BillPaymentInfo"
          icon="mdi-cards"
          :entity-name=" $t('付款记录')"
          :use-action="false"
          :use-select="false"
          :filter="filter"
        >
          <template #footer>
            <div/>
          </template>
        </ik-data-table>
      </v-col>
      <v-col
        id="billPayment"
        cols="4"
      >
        <base-material-card
          class="px-5 py-3 "
          color="indigo"
          icon="mdi-account-cash"
          inline
        >
          <template v-slot:after-heading>
            <div class="display-2 font-weight-light">
              {{ $t('订单信息') }}
            </div>
          </template>
          <v-data-table
            :fixed-header="false"
            :headers="infoHeaders"
            :items="infoList"
            :loading="loading"
            :items-per-page="30"
            hide-default-footer
            hide-default-header
          >
            <template v-slot:item.keyname="{ item }">
              <span class="font-weight-black">
                {{ item.keyname }}
              </span>
            </template>
            <template v-slot:footer>
              <v-toolbar
                flat
                dense
                extended
              >
                <v-btn dark>
                  <v-icon
                    left
                  >
                    mdi-arrow-left
                  </v-icon>
                  {{ $t('返回') }}
                </v-btn>
                <v-spacer/>
                <v-btn
                  v-print="'#detail'"
                  color="green darken-1"
                  dark
                  @click="reprintOrder"
                >
                  {{ $t('补打订单') }}
                </v-btn>
                <template #extension>
                  <v-dialog
                    v-model="dialog"
                    max-width="600"
                  >
                    <template v-slot:activator="{ on, attrs }">
                      <v-btn
                        color="red lighten-2"
                        dark
                        v-bind="attrs"
                        v-on="on"
                      >
                        {{ $t('修改支付方式') }}
                      </v-btn>
                    </template>
                    <v-card>
                      <v-card-title>
                        {{ $t('修改支付方式') }} TOTAL:{{ totalPrice.toFixed(2) }}
                      </v-card-title>
                      <v-card-text>
                        <v-sheet>
                          <div class="paymentLogList">
                            <template v-for="(p,index) in newPaymentLog">
                              <div
                                :key="'log'+index"
                                class="d-flex"
                              >
                                {{ p.name }}:{{ p.price.toFixed(2) }}
                              </div>
                            </template>
                          </div>
                          <div class="d-flex justify-space-between align-center">
                            <v-text-field
                              v-model="newAmount"
                              label="Amount"
                              type="number"
                              min="0"
                              step="0.01"
                            />
                            <v-select
                              v-model="newId"
                              label="Art des Payment"
                              class="ml-4"
                              item-value="id"
                              item-text="name"
                              :items="payMethods"
                            />
                            <v-btn
                              :disabled="!newAmount||!newId"
                              color="primary"
                              class="ml-2"
                              @click="insertNewPaymentLog"
                            >
                              <v-icon left>
                                mdi-plus
                              </v-icon>
                              {{ $t('OK') }}
                            </v-btn>
                          </div>
                        </v-sheet>
                      </v-card-text>
                      <v-divider/>
                      <v-card-actions>
                        <v-btn
                          color="primary"
                          text
                          @click="newPaymentLog = []"
                        >
                          {{ $t('Cancel') }}
                        </v-btn>
                        <v-spacer/>
                        <v-btn
                          color="primary"
                          text
                          @click="updatePaymentLog"
                        >
                          {{ $t('完成') }}
                        </v-btn>
                      </v-card-actions>
                    </v-card>
                  </v-dialog>
                </template>
              </v-toolbar>
            </template>
          </v-data-table>
        </base-material-card>
      </v-col>
      <v-col
        cols="12"
        md="6"
        class="printText"
      >
        <base-material-card
          v-if="rawAddressInfo"
          class="px-5 py-3 "
          color="indigo"
          icon="mdi-account-cash"
          inline
        >
          <template v-slot:after-heading>
            <div class="display-2 font-weight-light">
              {{ $t('地址信息') }}
            </div>
          </template>
          <v-row>
            <v-col
              cols="12"
              md="12"
            >
              <address-info-block :address-info="rawAddressInfo"/>
            </v-col>
          </v-row>
        </base-material-card>
      </v-col>
      <v-col
        v-if="rawAddressInfo"
        cols="12"
        md="6"
        class="printText"
      >
        <base-material-card
          class="px-5 py-3 "
          color="indigo"
          icon="mdi-printer"
          inline
        >
          <template v-slot:after-heading>
            <div class="display-2 font-weight-light">
              {{ $t('打印订单') }}
            </div>
          </template>
          <v-row>
            <v-col
              cols="12"
              md="12"
              class="d-flex spaceBetween"
              style="width: 100%"
            >
              <div
                class="mr-2"
              >
                <v-alert
                  border="left"
                  colored-border
                  color="deep-purple accent-4"
                  elevation="2"
                >
                  {{ $t('此处打印的订单仅供参考,不可作为Rechnung使用') }}
                </v-alert>
              </div>

              <v-btn
                right
                large
                color="success"
                dark
                @click.stop="printDialogShow = true"
              >
                {{ $t('打印') }}
              </v-btn>
            </v-col>
          </v-row>
        </base-material-card>
      </v-col>
      <v-dialog
        v-model="printDialogShow"
        max-width="400px"
      >
        <v-card>
          <v-card-title class="headline">
            {{ $t('打印此订单?') }}
          </v-card-title>

          <v-card-text id="detail">
            <v-card-subtitle>{{ $t('地址信息') }}</v-card-subtitle>
            <v-divider/>
            <address-info-block :address-info="rawAddressInfo"/>
            <v-card-subtitle>{{ $t('订单信息') }}</v-card-subtitle>
            <v-divider/>
            <dish-info-block
              :render-list="true"
              :dish-orders="orders"
            />
          </v-card-text>

          <v-card-actions>
            <v-spacer/>

            <v-btn
              color="green darken-1"
              text
              @click="printDialogShow = false"
            >
              {{ $t('Cancel') }}
            </v-btn>

            <v-btn
              v-print="'#detail'"
              color="green darken-1"
              text
              @click="printDialogShow = false"
            >
              {{ $t('打印') }}
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </v-container>
</template>

<script>
  import { getDetailOrder, reprintOrder, updatePaymentLogs } from '../../components/Utlis/api'
  import i18n from '../../i18n'
  import AddressInfoBlock from '../../components/base/AddressInfoBlock'
  import DishInfoBlock from '../../components/base/DishInfoBlock'
  import DishModification from '../../components/base/DishModification'
  import BillPaymentInfo from '../../model/BillPaymentInfo'
  import Swal from 'sweetalert2'
  import { IkDataTable, IKUtils } from 'innerken-utils'
  import Payment from '../../model/Payment'
  import ConsumeTypeStatus from '../../model/ConsumeTypeStatus'

  export default {
    components: {
      DishModification,
      DishInfoBlock,
      AddressInfoBlock,
      IkDataTable,
    },
    props: {
      id: {
        default: '-1',
        type: [String, Number],
      },
    },
    data: function () {
      return {
        newId: null,
        newAmount: null,

        BillPaymentInfo: BillPaymentInfo,
        loading: false,
        orderId: this.id,
        billData: { billInfo: { paymentStatus: 0 } },
        printDialogShow: false,

        newPaymentLog: [],

        dialog: null,
        payMethods: [],

        rawAddressInfo: '',
        infoHeaders: [
          {
            value: 'keyname',
            align: 'left',
          },
          {
            value: 'value',
            align: 'right',
          },
        ],
        infoList: [],
        headers: [
          {
            text: this.$i18n.t('code'),
            align: 'start',
            sortable: false,
            value: 'code',
          },
          {
            text: this.$i18n.t('name'),
            value: 'name',
          },
          {
            text: this.$i18n.t('modification'),
            value: 'mod',
          },
          {
            text: this.$i18n.t('createTimestamp'),
            value: 'stamp',
          },
          {
            text: this.$i18n.t('price'),
            value: 'price',
          },
          {
            text: this.$i18n.t('count'),
            value: 'sumCount',
          },
          {
            text: this.$i18n.t('sumPrice'),
            value: 'totalPrice',
          },
          {
            text: this.$i18n.t('note'),
            value: 'note',
          },
        ],
        orders: [],
        defaultItem: {
          paymentMethodId: -1,
          name: '',
        },
        consumeTypeStatusList: [],
        consumeTypeStatus: '',
        paymentStatus: '',
      }
    },
    computed: {
      filter () {
        return { id: this.id }
      },
      totalPrice () {
        const total = this.billData.billInfo.totalPrice ?? 0
        return total - this.newPaymentLog.reduce((t, i) => {
          return parseFloat(t) + parseFloat(i.price)
        }, 0.0)
      },
    },
    mounted () {
      this.reloadItem(this.orderId)
    },
    methods: {
      insertNewPaymentLog () {
        this.newPaymentLog.push(
          IKUtils.deepCopy({
            id: this.newId,
            name: this.payMethods.find(i => i.id === this.newId)?.name ?? '',
            price: parseFloat(this.newAmount),
          }),
        )
        this.newId = null
        this.newAmount = null
      },
      loadDetail: function (id) {
        getDetailOrder(id).then(res => {
          this.billData = res
          this.orders = this.billData.billOrderInfo.map((item, index) => ({
            id: index,
            agNameArr: item.hasMod > 0 ? item.agName.split(',') : [],
            aNameArr: item.hasMod > 0 ? item.aName.split(',') : [],
            ...item,
          }))
          this.rawAddressInfo = JSON.parse(this.billData.billInfo.rawAddressInfo)
          this.paymentStatus = this.billData.billInfo.paymentStatus === '1' ? this.$i18n.t('已支付') : this.$i18n.t('未支付')
          this.consumeTypeStatus = this.consumeTypeStatusList.find(i =>
            parseInt(this.billData.billInfo.consumeTypeStatusId) === i.id).name
          this.infoList = [
            {
              keyname: this.$i18n.t('订单号'),
              value: this.billData.billInfo.id,
            },
            {
              keyname: this.$i18n.t('servantName'),
              value: this.billData.billInfo.servantName,
            },
            {
              keyname: this.$i18n.t('tableName'),
              value: this.billData.billInfo.tableName,
            },
            {
              keyname: this.$i18n.t('startTime'),
              value: this.billData.billInfo.startTime,
            },
            {
              keyname: this.$i18n.t('结账时间'),
              value: this.billData.billInfo.endTime,
            },
            {
              keyname: this.$i18n.t('payMethod'),
              value: this.billData.billInfo.payMethodName,
            },
            {
              keyname: this.$i18n.t('支付状态'),
              value: this.paymentStatus,
            },
            {
              keyname: this.$i18n.t('consumeTypeStatus'),
              value: this.consumeTypeStatus,
            },
            {
              keyname: this.$i18n.t('personCount'),
              value: this.billData.billInfo.personCount,
            },
            {
              keyname: this.$i18n.t('tipIncome'),
              value: this.billData.billInfo.tipIncome,
            },
            {
              keyname: this.$i18n.t('discount'),
              value: this.billData.billInfo.discountStr,
            },
            {
              keyname: this.$i18n.t('totalPrice'),
              value: this.billData.billInfo.totalPrice,
            },
          ]
        })
      },
      async reloadItem (id) {
        this.dialog = false
        this.$nextTick(() => {
          this.$refs.paymentLog.reload()
        })

        this.consumeTypeStatusList = await ConsumeTypeStatus.getList()
        this.payMethods = (await Payment.getList()).filter(p => p.id !== 0 && p.id !== 9)
        this.loadDetail(id)
      },
      async reprintOrder () {
        const type = await Swal.fire({
          title: this.$i18n.t('Bitte wählen Sie eine Rechnungsart.'),
          input: 'select',
          inputOptions: {
            1: this.$i18n.t('Normal Rechnung'),
            2: this.$i18n.t('Unternehmensrechnung-1'),
            3: this.$i18n.t('Unternehmensrechnung-2'),
          },
        })
        if (type.value) {
          type.value = parseInt(type.value)
          let withTitle = 0
          let printCount = 1
          if (type.value > 1) {
            withTitle = 1
          }
          if (type.value > 2) {
            printCount = 2
          }
          reprintOrder(this.id, withTitle, printCount)
        }
      },
      findLangInArray (langArray, target = i18n.locale) {
        return langArray.find((item) => {
          return item.lang.toLowerCase() === target.toLowerCase()
        })
      },
      goBack () {
        this.$router.history.go(-1)
        // this.$router.push({ name: '桌子管理' })
      },
      async updatePaymentLog () {
        await updatePaymentLogs(this.orderId, this.newPaymentLog)
        this.reloadItem(this.orderId)
      },
    },
  }
</script>

<style scoped>

</style>
